import React, { Component } from 'react';
import { connect } from 'react-redux'
import './Ticket.styles.scss';
import { ReactComponent as TicketSVG } from '../../assets/imgs/Ticket.svg';
import { toggleTicket } from '../../redux/ticket/ticket.actions';


class Ticket extends Component{
    constructor(props) {
        super(props);
    }

    render() {

        const { children, styles } = this.props

        return (
            <section className={`Ticket ${styles}`} onClick={this.props.toggleTicketAction} >
                <TicketSVG width='52px'/>
                <p className='Ticket__Text' >{ children }</p>
            </section>
        );
    }
};



const mapDispathToProps = dispatch => ({
    toggleTicketAction: () => dispatch(toggleTicket())
})

export default connect(null, mapDispathToProps)(Ticket);