import ticketTypes from './ticket.types';
const { TOGGLETICKET } = ticketTypes;

const initialState = {
    ticketIsOpen: true
}

const ticketReducer = (state = initialState, action) => {
    switch(action.type) {
        case TOGGLETICKET:
            return {
                ...state,
                ticketIsOpen: !state.ticketIsOpen
            }
        default:
            return state
    }
}

export default ticketReducer;