import React, {Component} from 'react';
import { connect } from 'react-redux';
import Modal from '../../containers/Modal/Modal.container';
import './Questionnaire.styles.scss'
import { modalToggleQuestionnaire } from '../../redux/modal/modal.actions';
import { zipCode, no, yes } from '../../redux/quoteForm/quoteForm.actions';
import QuestionnaireViewer from '../Modals/QuestionnaireViewer/QuestionnaireViewer.modal';
import InputBox from '../InputBox/InputBox.component';
import Ticket from '../Ticket/Ticket.component';
import check from '../../assets/imgs/check.png';
import x from '../../assets/imgs/x.png';

class Questionnaire extends Component {
    constructor(props) {
        super(props)
    }

    render() {

        const noModal = () => {
            this.props.modalToggle();
            return this.props.doesntOwnsHome();
        }

        const yesModal = () => {
            this.props.modalToggle();
            return this.props.doesOwnHome();
        }

        return (
            <section className={`Questionnaire ${this.props.style}`} >
                {this.props.ticketIsOpen && this.props.windowWidth < 768 ? <Ticket >Exit</Ticket> : null}
                <section className='Questionnaire__Container' >
                    <section className='Questionnaire__Container__Holder' >
                        <h2 className='Questionnaire__Container__Holder__Title' >Do you own your home?</h2>
                        <section className='Questionnaire__Container__Holder__Options' >
                            <section className='Questionnaire__Container__Holder__Options__Container' >
                                <section className='Questionnaire__Container__Holder__Options__Container__Zip' >
                                    <InputBox placeholder='Zip Code' />
                                </section>
                                <section className='Questionnaire__Container__Holder__Options__Container__Options' >
                                    <section className='Questionnaire__Container__Holder__Options__Container__Options__Yes' onClick={yesModal} >
                                        <img src={check} alt='check' className='Questionnaire__Container__Holder__Options__Container__Options__Yes__Img' />
                                        <p className='Questionnaire__Container__Holder__Options__Container__Options__Yes__Text' >yes</p>
                                    </section>
                                    <section className='Questionnaire__Container__Holder__Options__Container__Options__No' onClick={noModal} >
                                        <img srcSet={x} alt='x' className='Questionnaire__Container__Holder__Options__Container__Options__No__Img' />
                                        <p className='Questionnaire__Container__Holder__Options__Container__Options__No__Text' >no</p>
                                    </section>
                                </section>
                            </section>
                                
                        </section>
                    </section>
                    
                </section>
                {
                    this.props.modal ?
                        <Modal >
                            <QuestionnaireViewer onClick={this.props.modalToggle} />
                        </Modal> :
                        null
                }
            </section>
        );
    }
};

const mapStateToProps = ({ modal: { modalQuestionnaireIsOpen }, windowWidth: { windowWidth }, ticket: { ticketIsOpen } }) => ({
    modal: modalQuestionnaireIsOpen,
    ticketIsOpen,
    windowWidth
})

const mapDispathToProps = dispatch => ({
    modalToggle: () => dispatch(modalToggleQuestionnaire()),
    zipCode: (zipCodeInfo) => dispatch(zipCode(zipCodeInfo)),
    doesntOwnsHome: () => dispatch(no()),
    doesOwnHome: () => dispatch(yes())
})

export default connect(mapStateToProps, mapDispathToProps)(Questionnaire);