import React, { Component } from 'react';
import Articles from '../Articles/Article.component';
import CTA from '../CTA/CTA.component';
import Pointer from '../Pointer/Pointer.component';
import Questionnaire from '../Questionnaire/Questionnaire.component';
import Title from '../Title/Title.component';
import './Hero.style.scss'
import { connect } from 'react-redux'
import Ticket from '../Ticket/Ticket.component';
import { windowWidthAction } from '../../redux/windowWidth/windowWidth.actions';

class Hero extends Component {
    constructor(props) {
        super(props);
    }

    handleResize = () => {
       return this.props.windowWidthTrigger(window.innerWidth);
    }

    componentDidMount() {
        window.addEventListener('resize', this.handleResize);
    }

    componentWillUnmount() {
        window.removeEventListener('resize', this.handleResize)
    }

    render() {
        return (
            <section className='Hero' >
                <section className='Hero__Left-Container' >
                    <Title >Go Green<br/>Save Green</Title>
                    {
                        this.props.windowWidth < 768 && this.props.ticketIsOpen ? <Questionnaire /> : null
                    }
                    {
                        this.props.windowWidth <768 && !this.props.ticketIsOpen ? <Ticket styles='Closed' >Do you<br />Qualify?</Ticket> : null
                    }
                    <Articles >
                        <Pointer >Fixed Rate pricing.</Pointer>
                        <Pointer >Pay half of what you pay right now.</Pointer>
                    </Articles>
                    <CTA >Schedule Now</CTA>
                </section>
                <section className='Hero__Right-Container' >
                    {
                        this.props.windowWidth >= 768 ? <Questionnaire /> : null
                    }
                </section>
            </section>
        );
    }


};

const mapStateToProps = ({ ticket: { ticketIsOpen }, windowWidth: { windowWidth } }) => ({
    ticketIsOpen,
    windowWidth
})

const mapDispathToProps = dispatch => ({
    windowWidthTrigger: (width) => dispatch(windowWidthAction(width))
})

export default connect(mapStateToProps, mapDispathToProps)(Hero);