import windowWidthTypes from './windowWidth.types';
const { WINDOWWIDTH } = windowWidthTypes;

const initialState = {
    windowWidth: 0
};

const windowWidthReducer = (state = initialState, action) => {
    switch (action.type) {
        case WINDOWWIDTH:
            return ({
                ...state,
                windowWidth: action.payload
            })    
        default:
            return state;
    }
};

export default windowWidthReducer;